.stats {
    position: fixed;
    top: 50%;
    right: 0%;
    transform: translate(0%, -50%);
    width: 24.5%;
    height: 90%;
  }

.pointEarned {
    height: 27.5%;
    background-color: #414141;
    margin-left: 10px;
    margin-right: 10px; 
    border-radius: 15px; 
    margin-bottom: 10px; 
    display: flex; 
    justify-content: space-evenly; 
    flex-direction: row;
}

.pointLost {
    height: 20%;
    background-color: #414141;
    margin-left: 10px;
    margin-right: 10px;
    border-radius: 15px;
    margin-bottom: 10px;
    display: flex;
    justify-content: space-evenly;
    flex-direction: row;
}

.half {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.title {
    color: #FFFFFF;
    font-weight: bold;
    font-size: 28px;
    margin: 0;
}

.textContent {
    color: #FFFFFF;
    font-weight: bold;
    font-size: 28px;
    margin: 0;
    flex: 0, 0, 120px;
}
