.container {
  background-color: #282c34;
  width: 100%;
  height: 100%;
  position: absolute;
  display: table;
}

.board {
  position: fixed;
  top: 50%;
  left: 0%;
  transform: translate(0%, -50%);
  width: 75%;
  height: 90%;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}